
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


img {
  display: block;
}
.home-container {
  display: flex;
  min-height: 100vh;
  overflow: hidden;
}

.container-1 {
  display: flex;
  min-width: 30%;
  max-height: 100vh;
  position: relative;
}



.container-2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 70%;

}


.container-1 > img{
  max-height: 100%;
  max-width: 100%;
}

.form-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  width: 100%;
}

.form-container > input[type=text] {
  display: flex;
  
}
.form-container > input[type=text]:invalid {
  border: 1px solid red;
}

.generic-input {
  all:unset;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  min-width: 100%;
  min-height: 2.5rem;
  border-radius: .5rem;
  border: 1px solid #CDCDCD;
  padding-left: 1rem;
  font-size: 1rem;
  margin-top: .5rem;
  text-align: left;
  
}

.generic-input::placeholder,
.box-sm::placeholder,
.box-md::placeholder {
  color: #CDCDCD;
}

.generic-input:focus,
.box-sm:focus,
.box-md:focus {
  border: 1px solid rgb(111, 164, 255);
}

.form-row {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

legend {
  font-weight: 700;
  font-size: .75rem;
}

.smaller-row {
  display: flex;
  gap: 1rem;
}


.box-sm {
  all: unset;
  border: 1px solid #CDCDCD;
  max-width: 3rem;
  min-height: 2.5rem;
  border-radius: .5rem;
  padding-left: 1rem;
  font-size: 1rem;
  margin-top: .5rem;

}
.gap-right {
  margin-right: 1rem;
}


.box-md {
  all: unset;
  border: 1px solid #CDCDCD;
  max-width: 13rem;
  min-height: 2.5rem;
  border-radius: .5rem;
  padding-left: 1rem;
  font-size: 1rem;
  margin-top: .5rem;

}
legend {
  color: rgb(42, 0, 42);
}
.submit-button {
  all:unset;
  background-color: rgb(42, 0, 42);
  min-width: 100%;
  min-height: 3rem;
  border-radius: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 1rem;
  cursor: pointer;
}

.submit-button:hover {
  background-color: rgb(131, 41, 131);
  transition: .25s;

}
input[data-error] {

}


.background-img {
  max-height: 100vh;
  min-width: 100%;
}

.card-container {
  position:relative;
  page-break-after: always;


}
.front-card {
  z-index: 3;
  bottom: -2rem;
  margin-left: 2rem;
}

.back-card {
  z-index: 2;
  right: 0;
  top: 2rem;
  margin-right: 2rem;
}
.card-logo,
.card-number,
.card-date,
.card-name {
  position: absolute;
}

.card-logo {
  top: 2rem;
  left: 2rem;

}

.card-name {
  bottom: 2rem;
  left: 2rem;
  color: white;
}

.card-number {
  bottom: 6rem;
  left: 2rem;
  color: white;
  font-size: 1.75rem;
  font-weight: 300;
}

.card-date {
  bottom: 2rem;
  right: 2rem;
  color: white;
}

.front-card,
.back-card {
  position: absolute;

}

.card-container {
}


.complete-container {
  min-width: 341px;
}

.complete-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}

.thanks {
  font-size: 1.75rem;
  color: rgb(42, 0, 42);
  font-weight: 400;

}

.complete {
  color: black;

}

@media  (max-width: 940px) {
  .home-container {
    flex-direction: column;
  }
  .container-1 {
    min-height: 30vh;
    max-height: 50vh;

  }
  .container-2 {
    padding-top: 4rem;
    padding-bottom: 4rem;

  }


}

@media (max-width: 400px) {
  .form-row {
    width: 80vw;
  }
  .smaller-row {
    flex-direction: column;
    gap: 1rem;
  }
  .box-sm {
    min-width: 20%;
  }


}

@media (min-width: 941px) {


  .front-card {
    z-index: 3;
    bottom: revert;
    margin-left: 0;
    top: 18%;
    left: 30%;
  }
  .back-card {
    z-index: 2;
    right: revert;
    top: revert;
    margin-right: 0;
    bottom: 18%;
    left: 40%;
  }

  .front-card,
  .back-card {
    position: block;
  }


}

.form {
  padding-left: 12rem;
  z-index: 99;
}


@media (max-width: 940px) {
  .form {
    padding-left: 0;
  }
}

